import React from 'react';
//import ReactDOM from 'react-dom';
import './App.css';

const API =
  "https://gist.githubusercontent.com/camperbot/5a022b72e96c4c9585c32bf6a75f62d9/raw/e3c6895ce42069f0ee7e991229064f167fe8ccdc/quotes.json";

class App extends React.Component {
  constructor(props) {
    super(props);
    this.state = { quotes: [], index: 0 };
  }

  componentDidMount() {
    fetch(API)
      .then((response) => response.json())
      .then((json) => {
        this.setState({ quotes: json.quotes });
      });
  }

  nextQuote = () => {
    const { quotes, index } = this.state;
    this.setState({ index: Math.floor(Math.random() * quotes.length) });
  };

  render() {
    const { quotes, index } = this.state;
    const quote = quotes[Math.floor(Math.random() * quotes.length)];
    console.log(index);

    return (
      <div id="wrapper">
        <h2 id="text">{quote && quote.quote}</h2>
        <h3 id="author">-{quote && quote.author}</h3>
     <div id = "button-holder">   
       <button id="new-quote" onClick={this.nextQuote}>
         <h3> New quote</h3>
        </button>
        <a
          id="tweet-quote"
          target="blank"
          href={`twitter.com/intent/tweet?text= ${quote && quote.quote} ${
            quote && quote.author
          }`}
        >
          <img
            id="icon"
            src="data:image/png;base64,"
          />
        </a></div>
      </div>
    );
  }
}


export default App;
